import React, { Component } from 'react'
import PropTypes from 'prop-types';
import axios from 'axios';
import Scream from '../components/scream/Scream';
import ScreamSkeleton from '../util/ScreamSkeleton'
import ProfileSkeleton from '../util/ProfileSkeleton'
import StaticProfile from '../components/profile/StaticProfile'
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import { getUserData } from '../redux/actions/dataActions';

class user extends Component {
    state = {
        profile:null,
        screamIdParam: null
    } 
    componentDidMount() {
        const handle = this.props.match.params.handle;
        const screamId = this.props.match.params.screamId;
        console.log(handle)
        if(screamId) this.setState({screamIdParam: screamId})

        this.props.getUserData(handle);
        axios.get(`/user/${handle}`)
            .then(res => {
                this.setState({
                    profile: res.data.user
                })
            })
            .catch(err => this.props.history.push('/errorUser'))
    }


    componentWillReceiveProps(nextProps) {
        console.log('here')
        console.log(nextProps.match.params.handle, this.props.match.params.handle)
        if(nextProps.match.params.handle !== this.props.match.params.handle) {
        const handle = nextProps.match.params.handle;
        this.props.getUserData(handle);
        axios.get(`/user/${handle}`)
            .then(res => {
                console.log(res.data.user)
                this.setState({
                    profile: res.data.user
                })
            })
            .catch(err => {this.props.history.push('/errorUser')})
    }
}

    render() {
        const {screams,loading} = this.props.data;
        const {screamIdParam} =this.state;
        const screamsMarkup = loading ? (
           <ScreamSkeleton />
        ) : screams === null ? (
            <p>No screams from this user</p>
        ) : !screamIdParam ? (
            screams.map(scream => <Scream key={scream._id} scream={scream} />)
        ) : (
            screams.map(scream => {
                if(scream.screamId !== screamIdParam)
                    return <Scream key={scream.screamId} scream={scream} /> 
                else return <Scream key={scream.screamId} scream={scream} openDialog />
            })
        )
        return (
            <Grid container spacing={16}>
                <Grid item sm={8} xs={12}>
                    {screamsMarkup}
                </Grid>
                <Grid item sm={4} xs={12}>
        { this.state.profile ? <StaticProfile profile={this.state.profile} />:<ProfileSkeleton /> }
                </Grid>
            </Grid>
        )
    }
}

user.propTypes = {
    getUserData: PropTypes.func.isRequired,
    data: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    data: state.data
})

export default connect(mapStateToProps,{getUserData})(user)
